import React from 'react';

import './App.css';
import Home from './Home';
import Nav from './Nav';
import Nyc from './Nyc';
import Cali from './Cali';
import Music from './Music';
import Vt from './Vt';

import {BrowserRouter as Router, Switch, Route} from 'react-router-dom';
function App() {
  return (
    <Router> 
   <div classname='App'>
 <Nav/>  
 <Switch>
 <Route path= {process.env.PUBLIC_URL + '/Home'}/>
 <Route path={process.env.PUBLIC_URL + '/Nyc'}/>
 <Route path={process.env.PUBLIC_URL + '/Cali'}/>
 <Route path={process.env.PUBLIC_URL + '/Music'}/>
 <Route path={process.env.PUBLIC_URL + '/Vt'}/>

 </Switch>
   </div>
   </Router>

  );
}

export default App;
